import './programming.css';
import { useEffect } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  NavLink,
} from 'react-router-dom';

import CSSPage from './programming/csspage';

var pagesName = ['csspage'];

var pageLink = () => {
  var pl = [];
  for (var pagename of pagesName) {
    var tmp = `NavLink className='table' to="/programming/${pagename}"`;
    pl.push(<tmp>{pagename}</tmp>);
  }
  return pl;
};

const ProgrammingPage = () => {
  useEffect(() => {
    document.title = 'Programming Page';
  }, []);

  return (
    <div className='programming'>
      <div className='programming_body'>
        <NavLink
          className='subpage-link'
          activeClassName='is-active'
          to='/programming/csspage'
        >
          csspage
        </NavLink>
        <Switch>
          <Route exact path='/programming/csspage'>
            <CSSPage />
          </Route>
          <Route>
            <div>
              <h1>Hello, this is a page for Programming</h1>
              <p>I will be updating this page soon!</p>
              <p>Those are the list of programming related pages!</p>
            </div>
          </Route>
        </Switch>
      </div>
    </div>
  );
};

export default ProgrammingPage;
